<script>
import domtoimage from "dom-to-image";
export default {
  components: {
    AswsomeBtn: () => import("@/components/btns/aswsome-btn.vue"),
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    /**
     * 生成
     */
    generate() {
      let image = document.querySelector("#image");
      domtoimage.toPng(image).then((dataUrl) => {
        let link = document.createElement("a");
        link.download = "my-image.png";
        link.href = dataUrl;
        link.click();
      });
    },
  },
};
