<template>
  <div class="list">
      <div class="item" v-for="(item,index) in list" :key="index">
          <aswsome-btn @onClick="jump(item.path)" :name="item.name"></aswsome-btn>
      </div>
      <!-- <div >
          按钮链接https://www.shejidaren.com/button-hover-and-click-effects.html
      </div> -->
  </div>
</template>

<script>

export default {
    components:{
        AswsomeBtn:()=>import("@/components/btns/aswsome-btn.vue")
    },
    data(){
        return{
            list:[
                {
                    name:"生成图片",
                    path:"/toImage"
                },{
                    name:"跳转导航",
                    path:"/navigation"
                }
            ]
        }
    },
    methods:{
        /**
         * 跳转
         */
        jump(path){
            this.$router.push(path)
        }
    }
}
</script>

<style>
.list{
    padding: 2rem;
}
.list .item{
 margin-bottom: 1rem;
}
